nav {
    display: block;
    height: 100%;
    position: relative;
}
nav>div {
    height: 100%;
}
nav ul {
    margin: 0;
    padding: 0;
}
nav>div>ul {
    list-style: none;
    transition: .5s all;
    display: inline-block;
    width: 100%;
    height: 100%;
    vertical-align: middle;
    position: relative;
    z-index: 202;
    zoom: 1;
}
#navigation {
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    text-transform: uppercase;
}
.mid_nav {
    outline: none;
}
nav>div>ul li {
    transition: .5s all;
    display: block;
    margin: 0;
    padding: 0;
    position: relative;
    z-index: 203;
}
nav>div>ul>li {
    float: left;
}
#navigation>li {
    transition: .5s all;

    height: 100%;
    margin: 0;
}
#navigation li {
    border-color: rgba(255,255,255,0);
}
ul li:first-child, ol li:first-child {
    padding-top: 0;
}
@media only screen and (min-width: 1025px){
