.register{
    background: -webkit-linear-gradient(left, #33cc33, #33ccff);
    
    padding: 3%;
  
   width: 100%;
    height: 100%;
    max-width: none;
    display: flex;
}
.register-left{
    align-self: start;
    text-align: center;
    color:#fff ;
    margin-top: 4%;
    width:30%

}
.register-left input{
    border: none;
    border-radius: 1.5rem;
    padding: 2%;
    width: 60%;
    background: #f8f9fa;
    font-weight: bold;
    color: #383d41;
    margin-top: 30%;
    margin-bottom: 3%;
    cursor: pointer;
}
.register-right{
    float: right;
    background: #f8f9fa;
    border-top-left-radius: 10% 50%;
    border-bottom-left-radius: 10% 50%;
    width:70%;
}
.register-left img{
    margin-top: 15%;
    margin-bottom: 5%;
    width: 50%;
    -webkit-animation: mover 2s infinite  alternate;
    animation: mover 1s infinite  alternate;
}
@-webkit-keyframes mover {
    0% { transform: translateY(0); }
    100% { transform: translateY(-20px); }
}
@keyframes mover {
    0% { transform: translateY(0); }
    100% { transform: translateY(-20px); }
}
.register-left p{
    font-weight: lighter;
    padding: 12%;
    margin-top: -9%;
}
.register .register-form{
    padding: 10%;
    margin-top: 10%;
}
.btnRegister{
    float: right;
    margin-top: 10%;
    border: none;
    border-radius: 1.5rem;
    padding: 2%;
    background: #0062cc;
    color: #fff;
    font-weight: 600;
    width: 50%;
    cursor: pointer;
}
.register .nav-tabs{
    margin-top: 3%;
    border: none;
    background: #0062cc;
    border-radius: 1.5rem;
    width: 28%;
    float: right;
}
.register .nav-tabs .nav-link{
    padding: 2%;
    height: 34px;
    font-weight: 600;
    color: #fff;
    border-top-right-radius: 1.5rem;
    border-bottom-right-radius: 1.5rem;
}
.register .nav-tabs .nav-link:hover{
    border: none;
}
.register .nav-tabs .nav-link.active{
    width: 100px;
    color: #0062cc;
    border: 2px solid #0062cc;
    border-top-left-radius: 1.5rem;
    border-bottom-left-radius: 1.5rem;
}
.register-heading{
    text-align: center;
    margin-top: 8%;
    margin-bottom: -15%;
    color: #495057;
    font-family: cursive;
}
.enteredName,.enteredEmail,.enteredPhone{
    box-sizing: border-box;
    width: 100%;
    position: relative;
    font-size: 10px;
    color: #ffb80e;
    padding:5px;
    border-radius: 3px;
    border: 1px solid #ffb80e;
    background-color: #fffbe8;
    
    display: inline-block;
}
.new-CloseButton {
    position: absolute;
    cursor: pointer;
    z-index: 9;
    
    
    width: 53px;
    height: 53px;
    text-align: center;
    background-color: #eee;
    border-radius: 50%;
    transition: all .3s ease-in-out;
}
/*register*/
 .loginRegisterForm {
    width: 100%;
    max-width: 500px;
}
@media only screen and (max-width: 768px)
{
 .loginRegisterForm {
    max-width: 100%;
}}


 .loginRegisterForm ul {
    margin: 0;
    padding: 0;
}
 .loginRegisterForm .signupSteps {
    display: none;
}
 .loginRegisterForm .signupSteps.activeStep {
    display: block;
}
 .loginRegisterForm ul li {
    display: block;
    margin: 15px 0;
    position: relative;
    list-style-type: none;
}
@media only screen and (max-width: 768px)
{
 .loginRegisterForm ul li {
    margin: 10px 0;
}}
 .loginRegisterForm ul li .field-labels {
    position: relative;
    display: inline-block;
    font-size: 14px;
    font-weight: 400;
    color: rgba(0,0,0,.7);
    margin: 0 0 10px;
}
 .loginRegisterForm ul li .errorMsgBox {
    float: right;
    height: 22px;
    min-width: 10px;
    margin: 0 0 4px;
}
 .loginRegisterForm ul li input {
    box-sizing: border-box;
    width: 100%;
    position: relative;
    font-size: 16px;
    height: 42px;
    border-radius: 3px;
    border: 2px solid #e5e5e5;
    background-color: #fff;
    padding: 0 13px;
}
@media only screen and (max-width: 768px)
{
 .loginRegisterForm ul li input {
    font-size: 14px;
    height: 42px;
}}
 .loginRegisterForm ul li .results-list {
    position: absolute;
    z-index: 9;
    top: 81px;
    left: 0;
    width: 100%;
    background: #fff;
    border: 1px solid rgba(0,0,0,.1);
}
@media only screen and (max-width: 768px)
{
 .loginRegisterForm ul li .results-list {
    top: 71px;
}}
 .loginRegisterForm ul li .results-list ol {
    margin: 0;
    padding: 0;
}
 .loginRegisterForm ul li .results-list ol li {
    display: block;
    list-style-type: none;
    padding: 5px;
    cursor: pointer;
    margin: 0;
    font-size: 13px;
}
 .loginRegisterForm ul li .submitButton {
    display: inline-block;
    vertical-align: middle;
    position: relative;
    width: 100%;
    height: 52px;
    text-align: center;
    color: #fff;
    font-size: 16px;
    text-transform: uppercase;
    border: none;
    padding: 8px 10px;
    transition: all .3s ease-in-out;
    box-shadow: 0 8px 15px #8a1818;
    border-radius: 3px;
    background-color: #b22222;
    overflow: hidden;
}
 .loginRegisterForm ul li .extraSpace {
    margin-top: 25px;
}
@media only screen and (max-width: 768px)
{
 .loginRegisterForm ul li .submitButton {
    float: left;
    height: 42px;
}}
 .loginRegisterForm ul li .submitButton:before {
    content: "";
    position: absolute;
    top: 0;
    left: -100%;
    height: 100%;
    width: 100%;
    background: rgba(0,0,0,.25);
    transition: all .3s ease-in-out;
}
 .loginRegisterForm ul li .submitButton.errorAnimation:before {
    opacity: 0;
    -webkit-animation: erroranimation .5s ease-in-out;
    animation: erroranimation .5s ease-in-out;
}
 .loginRegisterForm ul li .extraText {
    display: inline-block;
    font-size: 13px;
    color: rgba(0,0,0,.54);
    vertical-align: middle;
    margin: 20px 0 40px;
    width: 100%;
    text-align: center;
}
@media only screen and (max-width: 768px)
{
 .loginRegisterForm ul li .extraText {
    margin: 15px 0 20px;
}}
.loginRegisterForm ul li .orAction {
    display: inline-block;
    font-size: 14px;
    color: rgba(0,0,0,.9);
    vertical-align: middle;
    margin: 30px 0;
    width: 100%;
    text-align: center;
}
 .loginRegisterForm ul li .noMargin {
    margin-bottom: 0;
}
 .loginRegisterForm ul li .orAction button {
    color: #0071e4;
    border: none;
    background: none;
    cursor: pointer;
    text-decoration: none;
}

fieldset, legend {
    padding: 0;
    border: 0;
}
legend {
    display: block;
    width: 100%;
    margin-bottom: 20px;
    font-size: 21px;
    line-height: inherit;
    color: #333;
    border-bottom: 1px solid #e5e5e5;
}
 .loginRegisterForm ul li .ex-label {
    width: auto;
    position: absolute;
    display: inline-block;
    font-size: 14px;
    font-weight: 400;
    color: #ffb80e;
    z-index: 1;
    background-color: #fffbe8;
    bottom: 22px;
    left: 13px;
    padding: 3px 10px 0;
    border-radius: 3px;
}
 .loginRegisterForm ul li .enteredEmail {
    box-sizing: border-box;
    width: 100%;
    position: relative;
    font-size: 16px;
    color: #ffb80e;
    height: 52px;
    border-radius: 3px;
    border: 2px solid #ffb80e;
    background-color: #fffbe8;
    padding: 10px;
    display: inline-block;
}
 .loginRegisterForm ul li .redBorder input,  .loginRegisterForm ul li input.redBorder,  .loginRegisterForm ul li input:focus.ng-invalid {
    border: 1px solid #ff5c74;
}
 .loginRegisterForm .socialLogin {
    width: 100%;
    text-align: center;
    clear: both;
}
 .loginRegisterForm .socialLogin h4 {
    display: block;
    position: relative;
    height: 20px;
    margin: 0 0 10px;
}
 .loginRegisterForm .socialLogin h4:before {
    content: "";
    position: absolute;
    top: 10px;
    left: 0;
    border-bottom: 1px solid rgba(0,0,0,.15);
    width: 100%;
    z-index: 1;
}
 .loginRegisterForm .socialLogin h4 span {
    display: inline-block;
    font-size: 13px;
    color: rgba(0,0,0,.4);
    position: relative;
    z-index: 2;
    background: #fff;
    padding: 0 15px;
}
 .loginRegisterForm .socialLogin .socialIcon {
    box-sizing: border-box;
    display: inline-block;
    width: calc(33.33% - 10px);
    vertical-align: middle;
    height: 38px;
    line-height: 38px;
    border: 1px solid #3a5998;
    font-size: 16px;
    cursor: pointer;
    background: #fff;
    color: #3b5998;
    margin-bottom: 15px;
    transition: all .3s ease-in-out;
    border-radius: 5px;
}
 .loginRegisterForm .socialLogin .socialIcon em:before {
    line-height: 36px;
}

 .loginRegisterForm .socialLogin .socialIcon.googleIcon {
    font-size: 22px;
    margin: 0 8px 15px;
    color: #dc4e41;
    border: 1px solid #dc4e41;
}

.loginRegisterForm ul li .registerCheck p input {
    position: absolute;
    top: -50px;
    left: -100px;
    opacity: 0;
    z-index: -999;
}
 .loginRegisterForm ul li .registerCheck {
    width: 100%;
    clear: both;
    position: relative;
    overflow: hidden;
}
 .loginRegisterForm ul li .registerCheck p {
    margin: 0 0 15px;
    padding: 0 0 0 25px;
    font-size: 12px;
    position: relative;
    color: rgba(0,0,0,.65);
}
 .loginRegisterForm ul li .registerCheck p:last-child {
    margin: 0;
    color: #686868;
}
 .loginRegisterForm ul li .registerCheck p label {
    width: 16px;
    height: 16px;
    position: absolute;
    z-index: 9;
    left: 0;
    top: 0;
    border-radius: 2px;
    background: #fff;
    border: 1px solid #a7a7a7;
    display: block;
    float: left;
    margin: 0 12px 0 0;
    cursor: pointer;
    font-weight: lighter;
}
 .loginRegisterForm ul li .registerCheck p input:checked+label {
    background: #0071e4;
    border: 1px solid #0071e4;
}
 .loginRegisterForm ul li .registerCheck p label:before {
    font-weight: 900;
    font-family: "Font Awesome\ 5 Free";
    display: none;
    content: "\f00c";
    position: absolute;
    z-index: 2;
    top: 0;
    left: 2px;
    font-size: 10px;
    color: #fff;
}
 .loginRegisterForm ul li .registerCheck p input:checked+label:before {
    display: block;
}
 .loginRegisterForm ul li .registerCheck p a {
    color: #0071e4;
    text-decoration: none;
}
/*popup*/
.commonPopUp {
    opacity: 0;
    width: 100vw;
    height: 100vh;
    position: fixed;
    left: 0;
    top: 0;
    display: none;
    overflow-y: auto;
}
.commonPopUp.active {
    opacity: 1;
    z-index: 9999999;
    display: block;
}
.commonPopUp .popOverlay {
    background: transparent;
    width: 100%;
    height: 100%;
    z-index: 1;
}
.commonPopUp.active .popOverlay {
    background: rgba(0,0,0,.5);
    -webkit-animation: popupfade .2s ease-in-out;
    animation: popupfade .2s ease-in-out;
}
.commonPopUp .popHolder {
    background: #fff;
    position: absolute;
    left: 50%;
    top: 50%;
   
    z-index: 3;
    border-radius: 8px;
    
    display: flex;
    border-radius: 0;
    transform: none;
    
    
    width: calc(100% - 20px)!important;
    max-width: 740px!important;
    position: absolute;
    top: 50%!important;
    left: 50%!important;
    transform: translate(-50%,-50%)!important;
    height: auto!important;
    max-height: calc(100vh - 100px)!important;
    padding: 50px 30px!important;
    overflow-x: hidden!important;
    overflow-y: auto!important;
    border-radius: 10px!important;
}